.landing {
    grid-column: 1/-1;

    .banner {
        grid-template-areas: initial;

        .banner__image {
            grid-column: 1/-1;
        }

        .banner__standfirst {
            @include media('>tablet') {
                grid-column: 2/-2;
            }
            @include media('>between') {
                grid-column: 8/18;
            }
        }
    }

    .swiper-viewport {
        @include media('>between') {
            grid-template-areas: '. . . . . . . slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container';
        }
        @include media('<=between') {
            grid-template-areas: intial;

            .swiper-container {
                grid-column: 1/-1;
            }

            .swiper-buttons-wrap {
                right: $measure--basic;
                bottom: $measure--half;
            }
            .swiper-indicator {
                right: $measure--huge3;
                bottom: $measure--basic;
            }
        }
    }
    .statistics-block {
        @include media('<=mobile') {
        }
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }

    .featured-profiles-block {
        max-width: initial;
    }

    .embedded-video {
        @include standard-grid();

        .youtube-video {
            grid-column: 8/18;
            @include media('<=between') {
                grid-column: 2/-2;
            }
        }
    }

    .landing__top__cta {
        padding: $measure--medium3 0;
    }

    .cta-block {
        display: flex;
        .cta-block__cta {
            margin-right: $measure--basic;
        }
    }
}
.landing__top {
    margin-bottom: $measure--basic;

    > h2 {
        text-align: center;
    }

    .carousel-gallery-default {
        background-color: initial;
        @include standard-grid();

        .carousel-gallery {
            background-color: #3e1031;
            padding: $measure--basic;
            grid-column: 4/15;
        }
    }

    .figure--image {
        @include standard-grid();

        picture,
        figcaption {
            grid-column: 8/18;
            @include media('<=between') {
                grid-column: 2/-2;
            }
        }
    }
}
.landing__top__content {
    @include standard-grid();

    .landing__top__content--heading {
        grid-column: 8/18;
        @include media('<=between') {
            grid-column: 2/-2;
        }
    }
    .landing__top__content--text {
        grid-column: 8/18;
        @include media('<=between') {
            grid-column: 2/-2;
        }
    }
}

.landing__top__cta {
    display: flex;
    justify-content: center;
}

.landing__middle1 {
    @include standard-grid();

    .landing__middle1__pathway-image-cards {
        padding: $measure--small4;
    }

    > div {
        grid-column: 1/-1;
    }

    .accordion {
        grid-column: 8/18;
        padding-top: $measure--small4;
        padding-bottom: $measure--small4;

        @include media('<between') {
            grid-column: 1/-1;
        }
    }

    h2 {
        grid-column: 8/18;
        @include media('<=between') {
            grid-column: 2/-2;
        }
    }
    p {
        grid-column: 8/18;
        @include media('<=between') {
            grid-column: 2/-2;
        }
    }
}

.landing_middle1_content {
    @include standard-grid();

    margin: $measure--basic 0;

    .landing__middle1__content--heading {
        grid-column: 8/18;
        @include media('<=between') {
            grid-column: 2/-2;
        }
    }
    .landing__middle1__content--text {
        grid-column: 8/18;
        @include media('<=between') {
            grid-column: 2/-2;
        }
    }
}

.landing__middle2 {
    > h2 {
        text-align: center;
    }

    .swiper-viewport {
        padding-top: $measure--small4;
        padding-bottom: $measure--small4;
    }

    .slider__active-slide {
        @include media('>=between') {
            grid-column: 8/18;
        }
        @include media('<between') {
            grid-column: 2/-2;
        }
    }

    .slider__controls__wrap {
        @include media('>=between') {
            grid-column: 8/18;
        }
        @include media('<between') {
            grid-column: 2/-2;
        }
    }
}

.landing__whats-on {
    .related-content {
        @include media('>between') {
            @include standard-grid();
        }

        li:nth-of-type(1) {
            grid-column: 1 / span 8;

            @include media('>tablet', '<=between') {
                grid-column: 1/5;
            }
        }
        li:nth-of-type(2) {
            grid-column: 9 / span 8;
            @include media('>tablet', '<=between') {
                grid-column: 5/9;
            }
        }
        li:nth-of-type(3) {
            grid-column: 17/-1;
            @include media('>tablet', '<=between') {
                grid-column: 9/-1;
            }
        }
    }
}

.landing__bottom {
    @include media('>between') {
        h2 {
            text-align: center;
        }
    }
    @include media('>between') {
        .landing__bottom__related-content {
            .related-content {
                display: flex;
                flex-wrap: wrap;

                .card {
                    width: calc(1 / 2 * 100%);
                }
            }
        }
    }
}

.landing__contact {
    background-color: $color-pink80;
    padding: $measure--small4;
    margin-top: $measure--basic;

    @include media('>between') {
        @include standard-grid();
    }

    .landing__contact__sec {
        grid-column: 1 / span 16;
        color: $color-purewhite;

        .related-content {
            .card--contact__key-info {
                a {
                    color: $color-purewhite;
                    border-bottom-color: $color-purewhite;
                }
            }

            @include media('>between') {
                @include sixteen-grid();
            }

            li:nth-child(1) {
                grid-column: 1 / span 8;
                @include media('<=between') {
                    border-bottom: $border-width-default solid $color-pink70;
                }
            }

            li:nth-child(2) {
                grid-column: 9 / span 8;
            }
        }
    }

    .landing__contact__3rdcol {
        grid-column: 17 / span 8;

        .unibuddy-card {
            background-color: $color-purewhite;
        }

        .card--findus {
            background-color: $color-purewhite;

            &:hover {
                background-color: $color-purewhite;
            }
        }
    }

    .card--contact {
        background: initial;
        box-shadow: initial;
        padding: 0;
    }
}

.landing__contact__sec--heading {
    border-bottom: $border-width-default solid $color-pink70;
    h2 {
        text-align: left;
    }
}
